import type { Node } from '@ali/4ever-cangjie';
import Table from '../../mo/models';
interface IParam {
  node: Node;
  tableKey: string;
}

export default function isTableFromSameOrigin(
  _,
  { node, tableKey }: IParam,
) {
  // 其他情况pi才可能返回true
  if (Table.isTable(node) && node.key === tableKey) {
    return true;
  }
  return false;
}
